import React, { Component, PureComponent } from 'react';

import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, BarChart, Legend, Bar } from 'recharts';

import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

import { lodashToggle, jsDateTimeToStr, strToJsDateTime, jsDateTimeToTs, tsToStr, strDateToTs,
   tsToJsDatetime, strDateTimeToTs, tsToMoment } from '../utils';


export function genData (data, param) {
  let dataParams = []
  data.map(record => {
    if (record.record && record.record[param] )
      // dataParams = lodashToggle(dataParams, {ts: tsToStr(record.ts), [param]: record.record[param]})
      dataParams = lodashToggle(dataParams, {ts: record.ts, [param]: record.record[param]})
      // dataParams = lodashToggle(dataParams, {ts: tsToJsDatetime(record.ts), [param]: record.record[param]})
  })
  return dataParams
}

function genXticks (startDateTimeTs) {
  let ticks = []
  let startDateTimeStr = tsToStr(startDateTimeTs)
  startDateTimeStr = startDateTimeStr.split(':')[0] + ':00'
  startDateTimeTs = strDateToTs(startDateTimeStr)

  for (let i = 1; i < 27; i++) {
    let tick = startDateTimeTs + (i*60*60*1000)
    // ticks = lodashToggle(ticks, tsToStr(tick))
    ticks = lodashToggle(ticks, tick)
    // ticks = lodashToggle(ticks, tsToMoment(tick))
  }
  return ticks
}

function getModalStyle() {
  const top = 50 
  const left = 50

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    // width: 1000,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function ChartBody(props) {
  const { t1, t2, setDate, chartType, showChart, xTicks } = props
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);

  return (
    <div style={modalStyle} className={classes.paper}>
      <div className='d-flex justyfy-content-between mb-3'>
        <h4 className='mr-5'>{props.label}</h4>
        <div className=''>
          <TextField
            type="date"
            className='mr-2'
            InputLabelProps={{
              className: '',
              shrink: true 
            }}
            name='t1'
            value={t1}
            onChange={setDate}
          />
          <TextField
            type="date"
            InputLabelProps={{
              className: '',
              shrink: true 
            }}
            name='t2'
            value={t2}
            onChange={setDate}
          />
          <button className='btn btn-sm bg-blue2-light ml-1' onClick={showChart}>
            Показать
          </button>
        </div>
      </div>
      {chartType === 'bar'
        ? <RenderBarChart data={props.data} param={props.param} />
        : <RenderLineChart data={props.data} param={props.param} xTicks={xTicks}/>
      }
      
    </div>
  );
}

export class ChartModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      t1: null,
      t2: null,
      xTicks: []
    }
    this.setDate = this.setDate.bind(this);
    this.showChart = this.showChart.bind(this);
  }

  componentDidMount() {
      let t2 = new Date();
      let t1 = new Date().setDate(t2.getDate() - 1);
      let xTicks = []

      if (this.props.query.period === 'weekly') {
        t1 = new Date().setDate(t2.getDate() - 7);
      } else {
        xTicks = genXticks(t1)
      }
      
      let query = {ws: this.props.query.ws, sect: '', t1: t1, t2: jsDateTimeToTs(t2),
         param: this.props.query.param,}
      if (this.props.query.sect)
        query['sect'] = this.props.query.sect
      this.props.getESystemsDataDetail(query)

      this.setState({
        t1: tsToStr(t1).split('T')[0],
        t2: jsDateTimeToStr(t2).split('T')[0],
        xTicks: xTicks
      })
  }

  setDate (e) {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  showChart () {
    let query = {ws: this.props.query.ws, sect: '', 
        t1: strDateToTs(this.state.t1), 
        t2: strDateToTs(this.state.t2),
        param: this.props.query.param,}
    if (this.props.query.sect)
      query['sect'] = this.props.query.sect
    this.props.getESystemsDataDetail(query)
  }

  render () {
    const { detailDataList } = this.props
    const data = genData(detailDataList, this.props.query.param)
    const chartType = this.props.query.period === 'weekly' ? 'bar' : 'line'
    console.log(this.state)
    
    return (
      <ChartBody data={data} label={this.props.label} param={this.props.query.param}
        setDate={this.setDate} t1={this.state.t1} t2={this.state.t2} chartType={chartType}
        showChart={this.showChart} xTicks={this.state.xTicks}
        />
    )
  }
}

class CustomizedAxisTick extends PureComponent {
  render() {
    const { x, y, stroke, payload } = this.props;
    let date 
    let time

    console.log(payload)

    if (payload.value) {
      date = payload.value.split('T')[0]
      time = payload.value.split('T')[1]
    }

    return (
      <g transform={`translate(${x},${y})`}>
        <text x={0} y={0} dy={16} className='font-10'>
          {date}
        </text>
        <text x={0} y={0} dy={7}>
          {time}
        </text>
      </g>
    );
  }
}

class CustomizedAxisTickJsDT extends PureComponent {
  render() {
    const { x, y, stroke, payload } = this.props;
    let dateTime = jsDateTimeToStr(payload.value)
    let date 
    let time

    console.log('cust tick',payload)

    if (payload.value) {
      date = dateTime.split('T')[0]
      time = dateTime.split('T')[1]
      if (time.split(':')[1] !== '00')
        return null
    }

    return (
      <g transform={`translate(${x},${y})`}>
        <text x={0} y={0} dy={16} className='font-10'>
          {date}
        </text>
        <text x={0} y={0} dy={7}>
          {time}
        </text>
      </g>
    );
  }
}


class CustomizedAxisTickTS extends PureComponent {
  render() {
    const { x, y, stroke, payload } = this.props;
    let dateTime = tsToStr(payload.value)
    let date 
    let time

    if (payload.value) {
      date = dateTime.split('T')[0]
      time = dateTime.split('T')[1]
    }

    return (
      <g transform={`translate(${x},${y})`}>
        <text x={0} y={0} dy={16} className='font-10'>
          {date}
        </text>
        <text x={0} y={0} dy={7}>
          {time}
        </text>
      </g>
    );
  }
}

const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    return (
      <div className="custom-tooltip">
        <p className="label">{`${tsToStr(label)} : ${payload[0].value}`}</p>
      </div>
    );
  }

  return null;
};

