export function RenderLineChart (props) {
  const { data, param, xTicks } = props
  // xTicks
  // console.log(data)
  console.log(xTicks)
  return (
    <LineChart width={500} height={200} data={data}>
      <Line type="monotone" dataKey={param} stroke="#BF263C" />
      <CartesianGrid stroke="#ccc" />
      <XAxis 
        dataKey="ts" 
        // ticks={[0, 8, 16, 32, 40]}
        // minTickGap={10}

        // interval={0}
        // tickMargin={1}
        // tickCount={3}
        ticks={xTicks}
        // ticks={[
        //   strDateToTs("2021-06-22T00:00"),
        //   strDateToTs("2021-06-22T12:00"),
        //   strDateToTs("2021-06-23T23:00"),
        //   // "2021-06-22T00:00",
        //   // "2021-06-22T12:00",
        //   // "2021-06-23T23:00"
        //  ]}
        // scale='time'
        // domain={formatDomain}
        padding={{ left: 5, right: 5 }}
        type="number" 
        scale='time' 
        // domain = {[strDateToTs("2021-06-21T00:00"), strDateToTs("2021-06-23T23:00")]}
        domain = {['dataMin', 'dataMax']}
        // tickFormatter={formatXAxis}
        tick={<CustomizedAxisTickTS />} 
        />
      <YAxis ticks={[0, 5, 10, 20, 25, 30, 35]}/>
      <Tooltip 
        content={<CustomTooltip />}
        />
    </LineChart>
  )
}

function RenderBarChart (props) {
  const { data, param } = props
  return (
    <BarChart width={700} height={200} data={data}>
      <CartesianGrid strokeDasharray="3 3" />
      {/* <CartesianAxis y={10}/> */}
      <XAxis dataKey="ts" tick={<CustomizedAxisTickTS />} />
      <YAxis ticks={[1,2,3,4,5,6,7,8]} />
      <Tooltip />
      <Legend margin={{top: '50'}}/>
      <Bar dataKey={param} fill="#8884d8" />
    </BarChart>
  )
}